import React from 'react'
import { StaticQuery, graphql } from 'gatsby'

class Header extends React.Component {
    render() {
        console.log(this.props)
        return (
            <section id="header">
                <div className="inner">
                    <span className="icon major fa-cloud"></span>
                    <h1><strong>{this.props.values.h_title}</strong><br />
                    Ch'cloud day des gens du Nord</h1>
                    <p>Rendez vous le 19 novembre 2020</p>
                    <ul className="actions">
                        <li><a href="#one" className="button scrolly">Restons en contact</a></li>
                    </ul>
                </div>
            </section>
        )
           
    }
}

// export default Header

export default () => (
    <StaticQuery
        query={graphql`
        query {
            allMarkdownRemark {
                nodes {
                  frontmatter {
                    h_title
                    h_catchphrase
                  }
                }
            }
          }
        `}
        render={(data) => (
            <Header values={data.allMarkdownRemark.nodes.frontmatter} />
        )}
    />
)

